
import VHeader from '@/components/v-header/v-header'
import XNumber from 'vux/src/components/x-number/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'
import Popup from 'vux/src/components/popup/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

import Invoice from '@/pages/cart/cart-page/scenic-spot/invoice'
import { localUser } from '@/assets/js/local'
import axios from '@/api/axiosApi'
import { mapGetters } from 'vuex'
import Scroll from '@/components/scroll/scroll'

export default {
data () {
return {
show: false,
val: '未申请',
invoice: null,
hostelValue: 0,
menus1: {
menu1: '123',
menu2: '234'
},
totalPrice: 0,
totalDiscountPrice: 0,
remarks: '',
concat: localUser.get('realName') || localUser.get('nickname'),
phone: localUser.get('phoneNum')
}
},
computed: {
...mapGetters([
'selectOrder'
])
},
directives: {
TransferDom
},
components: {
VHeader,
XNumber,
XInput,
Cell,
Actionsheet,
Popup,
Group,
XButton,
Invoice,
Scroll
},
methods: {
change (val) {
if (this.selectOrder.type === '0') {
var a = this.moment(this.selectOrder.orderTimeLi)
var b = this.moment(this.selectOrder.orderTimee)
this.totalPrice = val * this.selectOrder.normalPrice * a.diff(b, 'days')
if (this.selectOrder.discount === 1) {
this.totalDiscountPrice = val * this.selectOrder.discountPrice * a.diff(b, 'days')
}
} else {
this.totalPrice = val * this.selectOrder.normalPrice
if (this.selectOrder.discount === 1) {
this.totalDiscountPrice = val * this.selectOrder.discountPrice
}
}
},
popupToggleSw (params) {
if (this.show === false) {
this.val = '未申请'
} else {
this.val = '已申请'
}
this.invoice = params
this.show = !this.show
},
orderSubmit () {
let param = {
productId: this.$route.params.packageId,

productNum: this.hostelValue,
goodsName: this.selectOrder.productName + '+' + this.selectOrder.ticketName + '套餐' + '+' + '共' + this.hostelValue + '张票',
goodsDesc: this.selectOrder.productDesc,
buyerId: localUser.get('id'),
effectiveDate: this.selectOrder.type === '1' ? this.selectOrder.orderTimee : this.selectOrder.orderTimee + '~' + this.selectOrder.orderTimeLi,
unitPrice: this.totalPrice,    // 优惠总价
invoice: 0,
originalPrice: this.totalDiscountPrice, // 原总价
phone: this.phone,
concat: this.concat,
remarks: this.remarks
}
if (this.hostelValue === 0) {
this.$vux.toast.text('请选择门票张数！', 'middle')
return
}
if (this.invoice != null) {
param.invoice = 1
param = Object.assign({}, param, this.invoice)
}
axios.put(this, '/v1/order/', param, (res) => {
this.$vux.toast.text('订单提交成功！', 'middle')
this.$router.push(`/user/order`)
this.$store.commit('SET_ORDER_TAB', 1)
})
}
}
}



